import "./styles.css";
import DataContext from "./context";
import Child from "./Child";
import { useState } from "react";

export default function App() {
  const [data, setData] = useState("Not Clicked");
  return (
    <div className="App">
      <DataContext.Provider value={data}>
        <Child />
        <button
          onClick={() => {
            setData("Clicked");
          }}
        >
          Click
        </button>
      </DataContext.Provider>
    </div>
  );
}
